
#homepage-vue-ap {
}

@media (min-width: 770px){
.home-grid {
    display: grid;
    grid-gap: 2%;
    grid-template-columns: repeat(2, 48%);
}
}

.action-button {
  border: 0;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none; /* for button */
     -moz-user-select: none;
      -ms-user-select: none;
  display: block;
  width: 100%;
  /* height: 3rem; */
  /* font-size: 1rem; */
  /* background: #DDD; */
  border: 2px solid #2a2a2a;
  font-weight: bold;
  color: #2a2a2a;
  background: transparent;
  text-transform: uppercase;
  padding: 10px 15px;
  font-size: 0.875rem;
}
.action-button:hover {
  border-color: #BE1E2D;
  color: #BE1E2D;
}
.action-button:focus {
  outline: none;
}

h3.mh-custom-posts-small-title {
  font-size: 1rem;
}
img.size-mh-magazine-small {
  width: 80px;
}

















